import React, { useEffect, useState } from "react";

export default function ReportsList({ token }) {
  const [reports, setReports] = useState([]);

 useEffect(() => {
  const fetchReports = async () => {
    try {
      const res = await fetch("http://localhost:5000/reports", {
        headers: { Authorization: `Bearer ${token}` },
      });
      const data = await res.json();
      setReports(Array.isArray(data) ? data : []);  //  ensure always array
    } catch (err) {
      console.error(err);
      setReports([]);  // fallback safe
    }
  };
  fetchReports();
}, [token]);


  return (
    <div className="full-page">
      <h3>All Reports</h3>
      {reports.length === 0 ? (
        <p>No reports found.</p>
      ) : (
        <table className="table table-bordered full-width-table">
          <thead>
            <tr>
              <th>Week</th>
              <th>Date</th>
              <th>Topic</th>
              <th>Outcomes</th>
              <th>Students Present</th>
              <th>Recommendations</th>
            </tr>
          </thead>
          <tbody>
            {reports.map((r) => (
              <tr key={r.report_id}>
                <td>{r.week}</td>
                <td>{r.lecture_date}</td>
                <td>{r.topic_taught}</td>
                <td>{r.learning_outcomes}</td>
                <td>{r.actual_students_present}</td>
                <td>{r.lecturer_recommendations}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
