import React, { useEffect, useState } from "react";
import { PieChart, Pie, Cell, Tooltip, Legend } from "recharts";

export default function Dashboard({ role, token }) {
  const [stats, setStats] = useState({
    users: 0,
    reports: 0,
    ratings: 0,
  });

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const res = await fetch("http://localhost:5000/stats", {
          headers: { Authorization: `Bearer ${token}` },
        });
        const data = await res.json();

        if (!res.ok && data.error) {
          console.error("Stats fetch error:", data.error);
          return;
        }

        setStats({
          users: data.users || 0,
          reports: data.reports || 0,
          ratings: data.ratings || 0,
        });
      } catch (err) {
        console.error("Stats fetch failed:", err);
      }
    };

    fetchStats();
  }, [token]);

  const data = [
    { name: "Users", value: stats.users },
    { name: "Reports", value: stats.reports },
    { name: "Ratings", value: stats.ratings },
  ];

  const COLORS = ["#2563eb", "#16a34a", "#facc15"];

  return (
    <div>
      <h2 className="mb-4">Dashboard - {role}</h2>

      {/* Summary Cards */}
      <div className="summary-grid">
        {Object.keys(stats).map((key) => (
          <div key={key} className="card-summary">
            <h3>{key.charAt(0).toUpperCase() + key.slice(1)}</h3>
            <p>{stats[key]}</p>
          </div>
        ))}
      </div>

      {/* Chart */}
      <div className="chart-container">
        <h4>System Overview</h4>
        <PieChart width={400} height={300}>
          <Pie data={data} dataKey="value" nameKey="name" outerRadius={100} label>
            {data.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
          <Tooltip />
          <Legend />
        </PieChart>
      </div>
    </div>
  );
}
