import React, { useState } from "react";

export default function ReportForm({ token }) {
  const [form, setForm] = useState({
    class_id: "",
    lecturer_id: "",
    week: "",
    lecture_date: "",
    topic_taught: "",
    learning_outcomes: "",
    actual_students_present: "",
    lecturer_recommendations: "",
  });
  const [message, setMessage] = useState("");

  const handleChange = (e) =>
    setForm({ ...form, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage("");
    try {
      const res = await fetch("http://localhost:5000/reports", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(form),
      });
      const data = await res.json();

      if (!res.ok) {
        setMessage(data.error || "Submission failed");
        return;
      }

      setMessage("Report submitted successfully!");
      setForm({
        class_id: "",
        lecturer_id: "",
        week: "",
        lecture_date: "",
        topic_taught: "",
        learning_outcomes: "",
        actual_students_present: "",
        lecturer_recommendations: "",
      });
    } catch (err) {
      setMessage("Server error. Try again.");
    }
  };

  return (
    <div className="full-page">
      <h3>Submit Lecture Report</h3>
      {message && <p className="info">{message}</p>}
      <form onSubmit={handleSubmit} className="full-form">
        <input
          name="class_id"
          placeholder="Class ID"
          value={form.class_id}
          onChange={handleChange}
          required
        />
        <input
          name="lecturer_id"
          placeholder="Lecturer ID"
          value={form.lecturer_id}
          onChange={handleChange}
          required
        />
        <input
          name="week"
          placeholder="Week"
          value={form.week}
          onChange={handleChange}
          required
        />
        <input
          type="date"
          name="lecture_date"
          value={form.lecture_date}
          onChange={handleChange}
          required
        />
        <input
          name="topic_taught"
          placeholder="Topic"
          value={form.topic_taught}
          onChange={handleChange}
          required
        />
        <textarea
          name="learning_outcomes"
          placeholder="Learning Outcomes"
          value={form.learning_outcomes}
          onChange={handleChange}
          required
        />
        <input
          name="actual_students_present"
          placeholder="Students Present"
          value={form.actual_students_present}
          onChange={handleChange}
          required
        />
        <textarea
          name="lecturer_recommendations"
          placeholder="Recommendations"
          value={form.lecturer_recommendations}
          onChange={handleChange}
        />
        <button className="btn btn-primary">Submit</button>
      </form>
    </div>
  );
}
