import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import SidebarLayout from "./components/SidebarLayout";
import Login from "./components/Login";
import Register from "./components/Register";
import Dashboard from "./components/Dashboard";
import ReportsList from "./components/ReportsList";
import Ratings from "./components/Ratings";
import Monitoring from "./components/Monitoring";
import ReportForm from "./components/ReportForm";

function ProtectedRoute({ children }) {
  const token = localStorage.getItem("token");
  if (!token) return <Navigate to="/login" />;
  return children;
}

function App() {
  const [role, setRole] = useState(null);
  const [token, setToken] = useState(null);

  useEffect(() => {
    const savedToken = localStorage.getItem("token");
    const savedRole = localStorage.getItem("role");
    if (savedToken && savedRole) {
      setToken(savedToken);
      setRole(savedRole);
    }
  }, []);

  return (
    <Router>
      <Routes>
  <Route path="/" element={<Navigate to="/login" />} /> {/* Redirect root */}

  {/* Public Routes */}
  <Route path="/login" element={<Login setAuth={setRole} setToken={setToken} />} />
  <Route path="/register" element={<Register />} />

  {/* Protected Routes */}
  <Route
    path="/"
    element={
      <ProtectedRoute>
        <SidebarLayout />
      </ProtectedRoute>
    }
  >
    <Route path="dashboard" element={<Dashboard role={role} token={token} />} />
    <Route path="reports" element={<ReportsList token={token} />} />
    <Route path="ratings" element={<Ratings token={token} />} />
    <Route path="monitoring" element={<Monitoring token={token} />} />
    <Route path="reportform" element={<ReportForm token={token} />} />
  </Route>
</Routes>

    </Router>
  );
}

export default App;
